<template>
  <div>
    <Loadings v-if="data ==null"></Loadings>
    <div class="container" v-else>
      <div class="list ">
        <div>
          <div class="item">
            <div class="info">
              <div class="title flex-sb">
                客户名称: {{data.customer.company}}
                <span class="time">{{formatDate(data.order.cr_time)}}</span>
              </div>
              <div>合同号: {{data.order.contract_no}}</div>
              <div>客户电话: {{data.order.tele_phone}}</div>
              <div>业务员: {{data.order.optioner}}</div>
              <div class="flex-sb">
                总数量
                <span>
                  {{data.order.num}}
                  <span>{{data.order.unit ? data.order.unit : '件'}}</span>
                </span>
              </div>
              <div class="flex-sb">
                总价格
                <span>
                  {{data.order.total_price}}
                  <span>元</span>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="list">
        <div class="caption">产品明细</div>
        <div>
          <div class="item" v-for="(item,indexs) in data.order_invoice_product" :key="indexs">
            <div class="info">
              <div class="title">
                货号{{item.number}}
                <span style="padding-left: 20px;">品名{{item.title}}</span>
              </div>
              <div class="tips">型号:{{item.model == '' ? '暂无': item.model}}</div>
              <div class="tips">产品属性:{{item.attribute == '' ? '暂无': item.attribute}}</div>
              <div class="tips">
                规格:{{item.standards}}
                <span style="padding-left: 20px;">单位:{{item.unit}}</span>
              </div>
               <div class="tips">{{item.explanfield ? item.explanfield :''}}</div> 
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getOrderDetail } from "@api/public";
import Empty from "@components/Empty";
import Loadings from "@components/Loadings";
import { Icon } from "vant";
export default {
  name: "MyMessage",
  components: {
    Empty,
    Loadings,
    [Icon.name]: Icon,
  },
  data: function () {
    return {
      data: null,
    };
  },
  created() {
    getOrderDetail({ order_no: this.$route.params.type }).then((res) => {
      this.data = res.data;
    });
  },
  mounted: function () {},
  methods: {
    formatDate: function (value) {
      let date = new Date(value * 1000);

      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;

      return y + "-" + MM + "-" + d + " ";
    },
  },
};
</script>
<style scoped>

.container {
  box-shadow: 0 0 5px #eee;
  background: #fff;
  border-radius: 5px;
}

.container {
  box-shadow: 0 0 5px #eee;

  margin-bottom: 10px;
  border-radius: 5px;
}
.container .mb10 {
  margin-bottom: 10px;
}

.container .caption {
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #eee;
  padding-left: 10px;

  font-size: 16px;
  color: #333;
}

.caption .month {
  height: 25px;
  width: 60px;
  background: #092e48;
  border-radius: 20px;
  color: #fff;
  line-height: 25px;
  font-size: 12px;
}

.caption .month span {
  display: inline-block;
  width: 23px;
  height: 23px;
  background: #fff;
  border-radius: 50%;
  margin-left: 1px;
  margin-right: 4px;
}

.container .list {
  line-height: 43px;
  font-size: 14px;
}
.container .list:first-child{
  border-bottom: 10px solid #eee;
}

.container .list .item {
  padding: 15px 8px;
  display: block;


}

.container .list > div {
  background-color: #fff;
}

.container .list img {
  width: 80px;
  margin-right: 10px;
}

.container .list .info {
  line-height: 18px;
  font-size: 12px;
  color: #999;
}

.container .list .info .title {
  font-size: 14px;
  margin-bottom: 5px;
  color: #333;
}

.container .list .item .right {
  padding-right: 15px;
  font-weight: bold;
}

.container .list .item .right span {
  font-size: 10px;
  font-weight: 500;
}

.price-num {
  text-align: center;
  font-size: 16px;

  color: #333;
  line-height: 20px;
}

.price-num span {
  font-size: 12px;
}

.price-num p {
  margin: 0 0 5px;
  font-size: 12px;
  color: #666;
}

.time {
  font-size: 12px;
  color: #999;
}
</style>
